/* Dropdown for Edit / New */
.edit-dropdown {
    display: flex;
    /* gap: 5px; */
    flex-direction: column;
    max-width: 500px;
    width: 100%;
    position: relative;
}
.dropdown-menu-edit {
    font-size: 0.9em;
    width: 100%;
    border: 0;
    padding: 5px 10px;
    border-radius: 8px;
    outline: none;
    /* border: 1px solid #cfcfcf; */
    border: 1px solid #dfdfdf;
    /* border: 1px solid rgb(222, 222, 222); */
    background: linear-gradient(#ffffff, #eaedf4);
}

.dropdown-options-edit {
    position: absolute;
    z-index: 1;
    background-color: white;
    top: 28px;
    left: 0;
    list-style: none;
    width: inherit;
    border-radius: 8px;
    border: 1px solid #cfcfcf;
    overflow: hidden;
}

.dropdown-btn-edit {
    position: absolute;
    height: 22px;
    width: 24px;
    font-size: 1.3em;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 0;
    background: none;
    /* border-radius: 5px; */

    top: 2px;
    left: calc(100% - 28px);
}


/* Dropdown for Logs ======================================================================================================================================================= */
.dropdown-menu {
    font-size: 0.85em;
    transition: all 250ms;
    height: fit-content;
    padding: 0 10px;

    height: 100%;
    min-height: 38px;
    border-radius: 0.2rem;
    outline: none;
    border: 1px solid #cfcfcf;
}
.dropdown-btn {
    position: absolute;
    height: 38px;
    width: 38px;
    font-size: 1.3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: none;
    border: 0;

    top: 0;
    left: calc(100% - 38px);
}
.dropdown-options-logs {
    position: absolute;
    z-index: 1;
    background-color: white;
    top: 43px;
    left: 0;
    list-style: none;
    width: inherit;
    border-radius: 3px;
    border: 1px solid #cfcfcf;
    overflow: hidden;
}
.dropdown-item {
    padding: 10px;
}
.dropdown-item:hover:not(.dropdown-item-active) {
    background-color: #cde7ff;
    cursor: pointer;
}
.dropdown-item-active {
    background-color: #3f93e0;
    color: white;
}
.dropdown-item-active {
    cursor: pointer;
}
.dropdown-input {
    font-size: 0.85em;
    padding: 0 10px;
    height: 100%;
    min-height: 38px;
    border-radius: 0.2rem;
    outline: none;
    border: 1px solid #cfcfcf;
}

/* Grid Structure ============================================================================================================ */
.logs-grid-inputs {
    /* margin: 40px auto auto; */
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-areas: 
        "opt opt"
        "dat tip"
        "btn btn"
    ;
    align-items: center;
    column-gap: 5px;
    row-gap: 2.5px;

    max-width: 500px;
    width: 100%;
    position: relative;

    /* background-color: red; */
}
.logs-margin-top {
    margin: 40px auto auto;
}
.fullw {
    row-gap: 5px;
    grid-template-areas: 
        "opt opt"
        "dat dat"
        "btn btn"
    ;
}
.fullw_rol{
    row-gap: 5px;
    grid-template-areas: 
        "opt opt"
        "inl inr"
        "btn btn"
    ;
}
.dropdown-menu {
    grid-area: opt;
}
.dropdown-date-input {
    grid-area: dat;
}
.dropdown-input-id {
    grid-area: dat;
}
.dropdown-input-left {
    grid-area: inl;
}
.dropdown-input-right {
    grid-area: inr;
}
.logs-btn {
    grid-area: btn;
}
