
/* popup =============================================================== */

.popup-background {
    background-color: #91919167;
    height: 100vh;
    width: 100%;
    z-index: 5;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}
.popup-container {
    background-color: white;
    width: 300px;
    margin: auto;
    position: relative;
    padding: 15px 20px;
    border-radius: 10px;
    transform: translateZ(0px);
}
.popup-title {
    font-size: 0.85em;
    margin-top: 3px;
}
.popup-close-btn {
    position: absolute;
    top: 8px;
    right: 8px;
    border: 0;
    display: flex;
    font-size: 0.9em;
    padding: 2px;
    background-color: transparent;
}
.popup-body {
    font-size: 0.76em;
    margin-top: 10px;
    padding: 0 2px;
}
.popup-btns {
    /* margin-top: 12px; */
    position: relative;
    width: fit-content;
    left: 100%;
    transform: translateX(-100%);
}
.popup-btns {
    margin-top: 8px;
    position: relative;
    width: fit-content;
    left: 100%;
    transform: translate(-96%, 28%);
}
.popup-continue-btn, .popup-cancel-btn {
    margin-left: 10px;
    margin-right: -5px;
    font-size: 0.8em;
    padding: 5px 14px;
    border-radius: 0.5rem;
    border: 0;
    background-color: var(--clr-blue);
    color: white;
}
.popup-cancel-btn {
    background-color: var(--clr-light-gray);
    color: black;
}

/* record popup ======================================================== */
.record-popup-background {
    background-color: #35363695;
    width: calc(100% - 250px);
    height: 100%;
    position: absolute;
    z-index: 1;
    cursor: pointer;
}
/* 250px es el width del sidebar, y 125px la mitad de eso */
.record-popup-container {
    position: absolute;
    top: 50%;
    left: calc(50% + 250px);
    transform: translate(calc(-50% - 125px), -50%);
    background-color: #edeff2;
    background-color: #f7f8fa;
    border: 1px solid #38393a81;
    border-radius: 8px;
    padding: 20px;
    height: 700px;
    width: 900px;
    max-width: calc(95% - 250px);
    position: fixed;
    z-index: 2;
    display: grid;
    grid-template-columns: 0.8fr 10px 1fr;
}
/* Estilo personalizado para los registros de rol_cobro */
.record-popup-container-rolcobro {
    left: calc(50% + 250px);
    transform: translate(calc(-50% - 125px), -50%);
    padding: 20px;
    width: 540px;
    max-width: calc(95% - 250px);
    display: block;
}
.record-popup-subtitle {
    padding: 5px 15px;
    margin-right: 10px;
    border-radius: 5px;

    background: linear-gradient(#92a5cb, #3d4f74);
    color: #ffffff;
    font-weight: normal;
}
.record-popup-table {
    width: calc(100% - 20px);
    text-align: left;
    table-layout: fixed;
    border-collapse: collapse;
    font-size: 0.8em;
    margin: 8px 0 15px 10px;
}
.record-popup-row {
    display: grid;
    grid-template-columns: 9.5em 1fr;
    column-gap: 2px;
}

.record-popup-row-rolcobro {
    grid-template-columns: 10.5em 1fr;
}
.record-popup-row > td {
    padding: 10px 8px;
    text-align: right;
    border-bottom: 1px dashed #e0e3e5;
    font-weight: 100;
}
.column > .pre-line {
    white-space: pre-line;
    text-align: left;
    padding: 0 8px 10px 20px;
}
/* esta clase es para separar el comentario en 2 filas en vez de 2 columnas \\ display: grid es necesario repetirlo para el RecordPopup de los Logs */
.column {
    display: grid;
    grid-template-columns: 1fr;
    column-gap: 0;
}
.column > th {
    grid-row: 1;
}
.column > td {
    grid-row: 2;
}
.record-popup-row > th {
    padding: 10px 8px;
    /* border-bottom: 1px solid #d3d6d8; */
    border-bottom: 1px dashed #e0e3e5;
    font-weight: 100;
}
.column > th {
    border-bottom: 0;
}
.record-popup-right {
    margin-left: 20px;
    overflow-y: auto;
}
.record-popup-left {
    overflow-y: auto;
}
.vertical-rule {
    border-right: 1px solid #d3d6d8;
}

