.list-header-title {
    display: flex;
    justify-content: center;
    border-radius: 5px;
    background: linear-gradient(#92a5cb, #3d4f74);
    color: #ffffff;
}

.list-container {
    border-collapse: collapse;
    font-size: 0.8em;
    text-align: left;
    width: 418px;
    max-width: 95%;
    margin: 20px auto 0 auto;
    table-layout: fixed;
}
.list-container > tr > th {
    white-space: nowrap;
    padding: 10px;
}
.list-container > tbody > tr {
    border-bottom: 1px dashed #e0e3e5;
}
.list-container > tbody > tr > td, .list-container > thead > tr > th {
    padding: 10px 10px;
    text-align: right;
    /* overflow-wrap: break-word; */
}
.list-container > tbody > tr > th {
    padding: 10px 10px;
    font-weight: normal;
}

.list-container > tbody > tr:not(.column) {
    display: grid;
    grid-template-columns: 10.5em auto;
}
.list-container > tbody > tr:nth-of-type(even) {
    /* background-color: #f1f3f7; */
}

.titulo-resultado {
    text-align: center;
    color: #f1f3f7;
    padding: 0 10px;
    border-radius: 50px;
    white-space: nowrap;
    font-weight: normal;
}
.result-list-rol {
    display: grid;
    grid-template-columns: auto 30px min-content;
    column-gap: 1px;
}
.list-items-btns {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    position: relative;
    width: fit-content;
    margin: auto;
    background-color: rgba(219, 222, 224, 0.485);
    background: linear-gradient(#92a5cb, #3d4f74);
    padding: 5px;
    border-radius: 5px;
    margin-top: 20px;
    width: 100%;
}
.list-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    border: 0;
    /* padding: 8px; */
    width: 30px;
    height: 30px;
    font-size: 1.25em;
    position: relative;
    background-color: #ffffff;
    color: var(--clr-blue2);
    border-radius: 50%;
}
.list-btn:active {
    box-shadow: inset 0px 0px 5px 3px rgba(93, 93, 93, 0.2);
}

/* pagination ========================================================== */
.rows-count {
    font-size: 0.8em;
    padding: 0 20px;
}
.separation-line {
    background-color: #b0b0b0;
    height: 20px;
    width: 1px;
}
.pagination {
    display: flex;
    flex-wrap: nowrap;
    gap: 5px;
    margin-left: 10px;
}
.pagination-page {
    width: 25px;
    height: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    background-color: transparent;
    border: 0;
    font-size: 0.8em;
}
.pagination-page:hover {
    cursor: pointer;
    background-color: #d8dde7;
}
.marked-page {
    background-color: #c0c7d3;
}

