/* insert list ========================================================= */

.marg-top-warning {
    margin: 25px 0;
}

.insert-body {
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    gap: 20px;
}

/* max width for mobile, then the layout has to change */
.insert-list-container {
    width: 418px;
    max-width: 100%;
    margin-left: auto;
    margin-right: auto;
}
.insert-list-body > tr {
    grid-template-columns: 8.5em auto !important;
}
.insert-list-body > tr > th {
    padding: 10px;
}
.text-right {
    text-align: right;
}
.insert-list-input-row {
    padding: 5px 5px !important;
    max-width: 100%;
    display: block;
}
.insert-list-input {
    font-size: 0.9em;
    width: 100%;
    border: 0;
    padding: 5px 10px;
    border-radius: 8px;
    outline: none;
    border: 1px solid rgb(222, 222, 222);
    background: linear-gradient(#ffffff, #eaedf4);
}
.insert-list-rol {
    display: grid;
    grid-template-columns: auto auto;
    gap: 5px;
}
.insert-list-rut {
    display: grid;
    grid-template-columns: auto 1em 4em;
    align-items: center;
}
.insert-list-rut > p {
    text-align: center;
}
.date-input::-webkit-calendar-picker-indicator {
    display: none;
}
textarea {
    max-height: 150px !important;
    min-height: 43px !important;
    resize: vertical;
}
.incomplete-field {
    border: 1px solid #fdcc78 !important;
}