/* loading animation =================================================== */

.loader {
    width: 100px;
    height: 100px;
    margin: 10px auto 20px auto;
    position: relative;
}

.loader-inner-1,
.loader-inner-2,
.loader-inner-3,
.loader-inner-4 {
    position: absolute;
    display: block;
    width: 10px;
    height: 10px;
    border-radius: 10px;
}

.loader-inner-1::before,
.loader-inner-2::before,
.loader-inner-3::before,
.loader-inner-4::before {
    position: absolute;
    content: '';
    display: block;
    width: 10px;
    height: 10px;
    border-radius: 10px;
    right: 0;
    animation: loading-1 2s infinite;
}

.loader-inner-1 {
    top: 0;
    left: 15px;
    transform: rotate(70deg);
}

.loader-inner-1::before {
    background-color: var(--clr-blue);
}

.loader-inner-2 {
    top: 15px;
    right: 0;
    transform: rotate(160deg);
}

.loader-inner-2::before {
    background-color: var(--clr-blue);
    background-color: #255fa5;
}

.loader-inner-3 {
    bottom: 0;
    right: 15px;
    transform: rotate(-110deg);
}

.loader-inner-3::before {
    background-color: var(--clr-blue);
}

.loader-inner-4 {
    bottom: 15px;
    left: 0;
    transform: rotate(-20deg);
}

.loader-inner-4::before {
    background-color: var(--clr-blue);
    background-color: #255fa5;
}

@keyframes loading-1 {
    0% {
        width: 10px;
        right: 0;
    }
    30% {
        width: 70px;
        right: -80px;
    }
    60% {
        width: 10px;
        right: -100px;
    }
    0% {
        width: 10px;
        right: 0;
    }
}

/* small loading animation =============================================== */

.loader.small {
    width: 40px;
    height: 40px;
    margin: 10px auto 20px auto;
    position: relative;
    transform: translateZ(0px);
}

.loader-inner-1.small,
.loader-inner-2.small,
.loader-inner-3.small,
.loader-inner-4.small {
    position: absolute;
    display: block;
    width: 5px;
    height: 5px;
    border-radius: 10px;
}

.loader-inner-1.small::before,
.loader-inner-2.small::before,
.loader-inner-3.small::before,
.loader-inner-4.small::before {
    position: absolute;
    content: '';
    display: block;
    width: 5px;
    height: 5px;
    border-radius: 10px;
    right: 0;
    animation: loading-2 2s infinite;
}

.loader-inner-1.small {
    top: 0;
    left: 5px;
    transform: rotate(70deg);
}

.loader-inner-1.small::before {
    background-color: var(--clr-blue);
}

.loader-inner-2.small {
    top: 5px;
    right: 0;
    transform: rotate(160deg);
}

.loader-inner-2.small::before {
    background-color: var(--clr-blue);
    background-color: #255fa5;
}

.loader-inner-3.small {
    bottom: 0;
    right: 5px;
    transform: rotate(-110deg);
}

.loader-inner-3.small::before {
    background-color: var(--clr-blue);
}

.loader-inner-4.small {
    bottom: 5px;
    left: 0;
    transform: rotate(-20deg);
}

.loader-inner-4.small::before {
    background-color: var(--clr-blue);
    background-color: #255fa5;
}

@keyframes loading-2 {
    0% {
        width: 5px;
        /* right: 0; */
        transform: translateX(0);
        
    }
    30% {
        width: 30px;
        /* right: -32px; */
        transform: translateX(32px);
        
    }
    60% {
        width: 5px;
        /* right: -45px; */
        transform: translateX(45px);
        
    }
    0% {
        width: 5px;
        /* right: 0; */
        transform: translateX(0);
        
    }
}