/* Todo esto de los < 'list-container-fullw' > corresponde a la vista de tabla, por lo que no hay que cambiarlo >>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>> */
/* full width list container =========================================== */
.justify-list-container {
    display: flex;
    justify-content: center;
    width: 100%;
}
.list-container-fullw {
    border-collapse: collapse;
    font-size: 0.8em;
    text-align: center;
    position: relative;
    table-layout: fixed;
    border-radius: 5px;
    width: inherit;
}
.list-container-fullw > tbody {
    display: inline-block;
    overflow-y: hidden;
    overflow-x: auto;
    width: inherit;
}
.list-container-fullw > tbody > .thead {
    display: grid;
    /* grid-template-columns: 1fr 1fr 1.5fr 1fr 1fr 1fr 1.5fr 1fr 0.5fr 1.5fr 1fr; */
    grid-template-columns: 7em 7em minmax(15em, 1.5fr) 10em 10em 8em minmax(15em, 1.5fr) minmax(10em, 1fr) minmax(5em, 0.5fr) minmax(15em, 1.5fr) minmax(10em, 1fr);
}
.list-container-fullw > tbody > .thead > th {
    background: linear-gradient(#92a5cb, #3d4f74);
    /* background: linear-gradient(#c3c8d2, #767980); */
    color: #ffffff;
    font-size: 0.8em;
    padding: 15px 20px;
    text-align: center;
    font-weight: 100;
}
/* .list-container-fullw > tbody > .thead > th:not(:first-child) {
    border-left: 0.1px solid rgba(185, 185, 185, 0.359);
} */
.list-container-fullw > tbody > .thead > th:first-child {
    border-radius: 5px 0 0 2px;
}
.list-container-fullw > tbody > .thead > th:last-child {
    border-radius: 0 5px 2px 0;
}
.list-container-fullw > tbody > .tbody {
    display: grid;
    grid-template-columns: 7em 7em minmax(15em, 1.5fr) 10em 10em 8em minmax(15em, 1.5fr) minmax(10em, 1fr) minmax(5em, 0.5fr) minmax(15em, 1.5fr) minmax(10em, 1fr);
    min-width: fit-content;
    width: 100%;
}
.list-container-fullw > tbody > .tbody:nth-of-type(odd) {
    background-color: rgba(229, 231, 235, 0.593);
}
.list-container-fullw > tbody > .tbody:hover {
    background-color: rgb(207, 210, 214);
    cursor: pointer;
}
.list-container-fullw > tbody > .tbody > td {
    font-size: 0.8em;
    padding: 12px 12px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 6em;

}
.list-container-fullw > tbody > .tbody > td:not(:first-child) {
    border-left: 0.1px solid rgba(218, 218, 218, 0.359);
}

/* Estilo personalizado para la tabla de rol_cobro ================================== */
.list-container-rolcobro {
    position: relative;
