/* pagination ========================================================== */
.rows-count {
    font-size: 0.8em;
    padding: 0 20px;
}
.separation-line {
    background-color: #b0b0b0;
    height: 20px;
    width: 1px;
}
.pagination {
    display: flex;
    flex-wrap: nowrap;
    gap: 5px;
    margin-left: 10px;
}
.pagination-page {
    width: 25px;
    height: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    background-color: transparent;
    border: 0;
    font-size: 0.8em;
}
.pagination-page:hover {
    cursor: pointer;
    background-color: #d8dde7;
}
.marked-page {
    background-color: #c0c7d3;
}

/* container para todos los elementos de la paginacion, ahora no se encuentra debajo de la lista, sino que encima */
.pagination-container {
    display: flex;
    justify-content: space-between;
}
.pagination-options {
    display: flex;
    /* background-color: red; */
    align-items: center;
    gap: 5px;
}
.pagination-options > label, input, button {
    font-size: 0.7rem;
}
.pagination-options > input {
    
}
.pagination-options > button {
    
}
.pagination-section {
    display: flex;
    justify-content: end;
    align-items: center;
    height: fit-content;
    padding: 10px 0 10px 0;
}

/* insert list ========================================================= */

/* para que se vea en 2 columnas */
.insert-body {
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    /* background-color: red; */
    gap: 20px;
}

/* este el el container de los input */
.insert-list-container {
    border-collapse: collapse;
    font-size: 0.8em;
    text-align: left;
    margin: 20px auto 0 auto;
    /* border: 0.1px solid #ededed; */
    table-layout: fixed;

    width: 418px;
    max-width: 100%;
    /* Creo que lo mejor seria cambiar los classname de todos los elementos de la lista, para diferenciar los estilos */
}
/* >>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>> */
.insert-list-container > tr > th {
    white-space: nowrap;
    padding: 10px;
}
.insert-list-container > thead > tr {
    border-radius: 5px;
    margin-bottom: 5px;
}
.insert-list-container > thead > tr > th {
    padding: 10px 10px;
    text-align: left;
    /* overflow-wrap: break-word; */
}
.insert-list-container > tbody > tr > th {
    padding: 10px 10px;
}
.insert-list-container > tbody > tr {
    display: grid;
    grid-template-columns: 10.5em auto;
    /* background-color: #ffffff; */
}
.insert-list-container > tbody > tr:nth-of-type(even) {
    /* background-color: #f1f3f7; */
}
/* >>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>> */
/* esto tiene que cambiar, ya que ahora los label estaran arriba del input */
.insert-list-body > tr {
    grid-template-columns: 8.5em auto !important;
}
.insert-list-body > tr > th {
    /* padding: 10px; */
    text-align: left;
    font-weight: 100;
    font-size: 0.85em;
    position: relative;
}
.insert-list-body > tr > th::after {
    content: ":";
    position: absolute;
    left: 95%;
}
.text-right {
    /* text-align: right; */
}
.insert-list-input-row {
    padding: 5px 5px !important;
    max-width: 100%;
    display: block;
}
/* estos son los input */
.insert-list-input {
    font-size: 0.9em;
    width: 100%;
    border: 0;
    padding: 5px 10px;
    border-radius: 8px;
    outline: none;
    /* border: 1px solid rgb(222, 222, 222); */
    border: 1px solid #dfdfdf;
    background: linear-gradient(#ffffff, #eaedf4);
}
.insert-list-rol {
    display: grid;
    grid-template-columns: auto auto;
    gap: 5px;
}
.insert-list-rut {
    display: grid;
    grid-template-columns: auto 1em 4em;
    align-items: center;
}
.insert-list-rut > p {
    text-align: center;
}
