.body-container {
    max-width: 95%;
    margin: auto;

    display: flex;
    flex-direction: column;
}

.form-consulta {
    margin-top: 25px;
    width: 100%;
}
.inputs-mobile {
    margin-top: 50px;
    border-radius: 8px;
}
.inputs-desktop {
    margin-top: 50px;
    box-shadow: 0px 0px 8px 1px rgba(100, 100, 100, 0.1);
    border-radius: 8px;
}
.form-input {
    font-size: 0.8em;
    width: 100%;
    padding: 10px 10px;
    border-radius: 8px 0 0 8px;
    outline: none;
    border: 1px solid #dcdcdc;
    /* background: linear-gradient(#ffffff, #e6e9ee); */
    /* background: linear-gradient(#ffffff, #e5e6e8); */
    background-color: #ecedf0;
    background-color: #fbfbfc;
    transition: background-color ease-in 150ms,
                border ease-in 150ms;
}
.form-input:focus {
    /* background-color: #eff9ff; */
    background-color: #fafdff;
    border: 1px solid var(--clr-blue);
}
.form-input:nth-of-type(even) {
    border-radius: 0.8em;
}

.right-form-input > input {
    border-radius: 0 !important;
}
.left-form-input > input {
    border-radius: 8px 0 0 8px !important;
}
.grid-inputs {
    display: grid;
    margin-left: auto;
    margin-right: auto;
    max-width: 420px;
    grid-template-columns: 1fr 100px;
}
.grid-inputs-even {
    grid-template-columns: 1fr 1fr 100px;
    column-gap: 0;
    grid-template-areas: 
        /* "lab1 inp1 inp2 btn1" */
        "inp1 inp2 btn1"
    ;
}
.vertical-grid-inputs {
    grid-template-columns: 1fr;
}
.form-label {
    grid-area: lab1;
    font-size: 0.8em;
    margin: auto;
    margin-right: 10px;
}
.input-left {
    grid-area: inp1;
}
.input-right {
    grid-area: inp2;
}
.btn-left-right {
    grid-area: btn1;
}
/* creo que esto no sirve, por los grid-template-areas, me 'parece' si que no es de los form */
.grid-inputs > .input > input:nth-of-type(even) {
    margin-left: 0;
    text-align: center;
}
.blue-btn {
    font-size: 0.9em;
    padding: 9px 18px;
    border-radius: 0.2rem;
    border: 0;
    background-color: var(--clr-blue);
    background: linear-gradient(rgb(141, 194, 255), var(--clr-blue));
    color: white;
}
.blue-btn:active {
    background-color: var(--clr-blue2);
    box-shadow: var(--shd-v2);
    box-shadow: inset 0px 0px 5px 3px rgba(41, 83, 146, 0.4);
}
.desktop-blue-btn {
    border-radius: 0 8px 8px 0;
}
.get-m2-text {
    margin: 10px;
    font-size: 0.85rem;
    color: #656565;
}
.titulo-consulta {
    color: #656565;
    border-bottom: 1px solid #d6d9dc92;
    padding: 12px;
    width: 100%;
    /* position: absolute; */
    text-align: center;
    top: 0;
    left: 0;
}
.input {
    position: relative;
}
.hint {
    position: absolute;
    font-size: 0.8em;
    color: var(--clr-dark-gray);
    left: 2%;
    top: -40%;
}
.text-center {
    text-align: center;
}

/* list ================================================================ ============================= ============================ ========================== ========================*/
.form {
    max-width: 100%;
    margin: auto;
}
.bug-placeholder {
    margin-top: 20px;
    padding: 20px;
    text-align: center;
    font-size: 0.8em;
    color: #c4c4c4;
}
.bug {
    font-size: 2em;
    transform: rotate(45deg);
}
