/* 
    de aqui saque todo el list-container, etc...
*/
.warning {
    font-size: 0.75em;
    color: #b4b4b4;
    margin-top: 20px;
    text-align: center;
}
.popup-crud-btns-container {
    display: flex;
    height: fit-content;
    gap: 6px;
    justify-content: end;
    margin-bottom: 10px;
    padding: 0 20px 0px 10px;
    /* background-color: red; */
}
.popup-crud-btn {
    /* background-color: transparent; */
    /* border: 1px solid #bfbfbf; */
    border: 0;
    border-radius: 3px;
    display: flex;
    width: fit-content;
    font-size: 1rem;
    padding: 2px;
}
.vertical-separation {
    background-color: #cecece;
    height: 20px;
    width: 1px;

}
.crud-actions-section {
    /* background-color: red; */
    display: flex;
    flex-wrap: nowrap;
}
.crud-actions-section > .warning {
    margin: auto;
    margin-left: auto;
    /* background-color: #3d4f74; */
    height: fit-content;
    padding-bottom: 10px;
}
.crud-btns-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 5px;
    max-width: 600px;
    width: 100%;
    margin: auto;
}
.crud-btn {
    font-size: 0.9em;
    padding: 9px 18px;
    border-radius: 0.2rem;
    border: 0;
    background-color: var(--clr-blue);
    margin-top: 10px;
    text-decoration: none;
}
.delete, .edit {
    /* background-color: #e8962c; */
    background: linear-gradient(#ffca84, #e8962c);
    color: white;
}
.delete {
    /* background-color: #de5e2f; */
    background: linear-gradient(#ffa685, #de5e2f);
}
.delete:active, .edit:active {
    box-shadow: var(--shd-btn);
}
/* quitar si no se va a usar .disabled-crud-btn ========================================================================================================= */
.disabled-crud-btn {
    cursor: not-allowed;
}
.save, .cancel {
    margin-top: 25px;
}
.save {
    background-color: var(--clr-blue);
    background: linear-gradient(rgb(141, 194, 255), var(--clr-blue));
    color: white;
}
.cancel {
    /* background-color: var(--clr-gray2); */
    /* background: linear-gradient(rgb(247, 247, 247), var(--clr-gray2)); */
    /* background-color: #f1f3f7; */
    background: linear-gradient(#eceff2, #cdd2da);
}
.save:active {
    box-shadow: inset 0px 0px 5px 3px rgba(41, 83, 146, 0.4);
}
.cancel:active {
    box-shadow: var(--shd-btn);
    color: #42454b;
}

/* donwload ============================================================ */

.download-container {
    width: fit-content;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 10em;
    margin: 5rem auto;
    cursor: pointer;
    border: 1px solid #dfdfdf;
    border-radius: 8px;
    padding: 1.5em 3em;
    background: #f9f9fa;
    box-shadow: var(--shd-v4);
    position: relative;
}
.excel-sheet-img {
    font-size: 4em;
    color: #0377c5;
}
.download-img {
    color: #f9f9fa;
    background-color: #0377c5;
    height: 2.5em;
    width: 2.5em;
    padding: 0.6em;
    overflow: visible;
    border-radius: 50%;
}
.download-text {
    max-width: 440px;
    font-size: 0.8em;
    position: absolute;
    top: 50%;
    left: 32%;
    font-weight: bold;
    color: #0377c5;
}
