/* logs container ============================================ */

.logs-container {
    display: flex;
    flex-direction: column;
}

.prevent-select {
    -webkit-user-select: none; /* Safari */
    -ms-user-select: none; /* IE 10 and IE 11 */
    user-select: none; /* Standard syntax */
}


@media (max-width: 749px) {
    .body-container {
        /* width: 500px; */
        /* margin-top: 35px; */

        margin: 35px auto 30px;
        padding: 0 30px 30px;
    }

    .form-input {
        border-radius: 0.2rem;
    }

    .grid-inputs {
        display: grid;
        column-gap: 5px;
        row-gap: 5px;
        margin-left: auto;
        margin-right: auto;
        grid-template-columns: 1fr;
    }

    .grid-inputs-even {
        column-gap: 5px;
        grid-template-columns: 1fr 1fr;
        grid-template-areas: 
            "inp1 inp2"
            "btn1 btn1"
        ;
    }
}